import { BrowserRouter, Routes, Route } from "react-router-dom";
import AskPage from "./components/AskPage";
import "./App.css";
import JokePage from "./components/JokePage";
const App = () => {
  return (
    <BrowserRouter>
      <Routes>
        <Route exact path="/:query" element={<AskPage />} />
        <Route exact path="/j/:query" element={<JokePage />} />
        <Route exact path="/" element={<JokePage />} />
        <Route path="*" element={<AskPage />} />
      </Routes>
    </BrowserRouter>
  );
};

export default App;
